<template>
    <div id="univbb-widget3" class="univbb-widget" v-bind:class="[options.dataset.type==='others' ? 'others' : '']">
        <!-- <p>{{data.game[0].length}}</p> -->
        <header v-if="options.dataset.type!=='others'">
            <h2></h2>
        </header>
        <div class="univbb-widget__body">
            <table>
                <tbody v-for="(week, index) in data.game">
                    <tr v-for="(date, index2) in week">
                        <th v-bind:rowspan="week.length" v-if="index2 === 0 && options.dataset.type !== 'others' ">{{date[0].week}}</th>
                        <td><span class="date">{{date[0].dateM}}/{{date[0].dateD}}({{date[0].weekday}})</span><br><span class="time">{{date[0].time}}</span></td>
                        <td class="game_wrap clearfix" v-bind:class="[date.length >= 3 ? 'over3' : '']">
                            <div class="inner_box" v-for="(game, index3) in date">
                                <p class="game_num">{{(index3 + 1)}}</p> 
                                <p class="card">
                                    <span class="name">
                                        <img v-bind:src="'/stats/assets/univbb/img/logo_'+game.team[0].nameI+'_s.png'" alt="" v-if="options.dataset.type !== 'others'">
                                        {{game.team[0].name}}
                                        <span v-if="options.dataset.type === 'others'">{{game.team[0].team_name}}</span>
                                    </span>
                                    <span class="score">{{game.team[0].score}}
                                        <span v-if="game.endpoint">-</span>
                                        <span v-else>VS</span>
                                    {{game.team[1].score}}</span>
                                    <span class="name">
                                        {{game.team[1].name}}
                                        <span v-if="options.dataset.type === 'others'">{{game.team[1].team_name}}</span>
                                        <img v-bind:src="'/stats/assets/univbb/img/logo_'+game.team[1].nameI+'_s.png'" alt="" v-if="options.dataset.type !== 'others'">
                                    </span>
                                </p>
                                <div class="link" v-if="options.dataset.type !== 'others'">
                                    <p class="btn detail active" v-if="game.endpoint"><a v-bind:href="'/big6tv/' +  options.dataset.season + '/game/' + game.id"><span class="active"></span></a></p>
                                    <p class="btn detail non_active" v-else><span class="non_active"></span></p>
                                    <p class="btn highlight active" v-if="game.highlightmovie"><a v-bind:href="game.highlightmovie"><span class="active"></span></a></p>
                                    <p class="btn highlight non_active" v-else><span><span></span></span></p>
                                </div>
                                <div class="link-others" v-if="options.dataset.type === 'others'">
                                    <span v-if="game.endpoint === ''" v-bind:class="[game.endpoint ? 'btn-active' : 'btn-non-active']"></span>
                                    <a v-if="game.endpoint" v-bind:href="'/stats/' + options.dataset.league  + '/' +  options.dataset.season + '/game/' + game.id" v-bind:class="[game.endpoint ? 'btn-active' : 'btn-non-active']"></a>
                                </div>
                            </div>
                        </td>
                    </tr>
                </tbody>
            </table>
        </div>
    </div>
</template>
<script>
    import request from 'superagent';
    import {arg} from './js/common';
    import {ApiFormatSchedule} from './js/config_2';
    export default {
        name: 'univbb-widget3',
        data() {
            return {
                data: ApiFormatSchedule,
                options: {
                    dataset: document.getElementById('univbb-widget3').dataset,
                }
            }
        },
        computed: {
        },
        created() {
            this.getData();
        },
        methods: {
            getData: function() {
                const _that = this;
                const root = 'https://s3-ap-northeast-1.amazonaws.com/ublive.sportsbull.jp/json/';
                const data = _that.options.dataset;
                // console.log(root + data.league + '/' + data.season + '/' + 'schedule.json');
                request
                .get( root + data.league + '/' + data.season + '/' + 'schedule.json')
                .end(function(err, res) {
                    _that.data = res.body;
                    // console.log(_that.data);
                });
            }
        }
    }
</script>
<style lang="scss" scoped>
    @import './css/cleanslate.scss';
    @import './css/reset.scss';
