                                    position: relative;
                                    float: left;
                                    &:nth-of-type(odd) {
                                        padding-right: 30px;
                                    }
                                    &:nth-of-type(even) {
                                        padding-left: 30px;
                                    }
                                    &:nth-of-type(odd) {
                                        &:before, &:after {
                                            content: "";
                                            position: absolute;
                                            width: 1px;
                                            background-color: #999999;
                                            height: 100%;
                                            top: 0;
                                            bottom: 0;
                                        }
                                        &:before {
                                            left: -25px;
                                            @media only screen and (max-width : 768px){
                                                left: -5px;
                                            }
                                        }
                                        &:after {
                                            right: 0;
                                        }
                                    }
                                    &:nth-of-type(n + 3) {
                                        margin-top: 10px;
                                        // padding-top: 10px;
                                        // border-top: 1px solid #666;
                                        &:before {
                                            height: calc(100% - 10px);
                                        }
                                        &:after {
                                            content: "";
                                            position: absolute;
                                            // width: calc(100%);
                                            width: 1px;
                                            border-top: 1px solid #999;
                                            height: 100%;
                                            // left: 0;
                                            top: 0;
                                        }
                                    }
                                    &:nth-of-type(4) {
                                       &:after {
                                            // width: calc(100% + 25px);
                                            display: none;
