                                content: "";
                                clear: both;
                                display: block;
                                width: 100%;
                                background-color: #999999;
                                height: 1px;
                                position: absolute;
                                top: 0;
                                bottom: 0;
                                right: 0;
                                margin: auto;
                            }
                            td {
                                position: relative;
                                padding: 10px 25px 10px 25px;
                                vertical-align: middle;
                                text-align: center;
                                // &:nth-of-type(1):before {
                                //     content: "";
                                //     width: 1px;
                                //     height: calc(100% - 20px);
                                //     border-right: 1px solid #999;
                                //     position: absolute;
                                //     top: 0;
                                //     bottom: 0;
                                //     right: 0;
                                //     margin: auto;
                                // }
                                .date {
