                                        border-radius: 3px;
                                        position: relative;
                                        span {
                                            font-size: 1.4em;
                                            width: 75px;
                                        }
                                        a:before {
                                            content: " ";
                                            background-image: url(/stats/assets/univbb/img/icon-highlight_on.png);
                                            background-repeat: no-repeat;
                                            background-size: 14px;
                                            width: 14px;
                                            height: 14px;
                                            display: inline-block;
                                            position: relative;
                                            margin-right: 3px;
                                        }
                                        &:hover {
                                            a:before {
                                                background-image: url(/stats/assets/univbb/img/live_play_btn.png);
                                            }
                                        }
                                    }
                                    .btn.highlight.non_active {
                                        border: 1px solid #666;
                                        border-radius: 3px;
                                        position: relative;
                                        color: #666;
                                        span {
                                            span {
                                                font-size: 1.4em;
                                            }
                                        }
                                        >span:before {
                                            content: " ";
                                            background-image: url(/stats/assets/univbb/img/icon-highlight_off.png);
                                            background-repeat: no-repeat;
                                            background-size: 14px;
                                            width: 14px;
                                            height: 14px;
                                            display: inline-block;
                                            position: relative;
                                            margin-right: 3px;
                                        }
                                    }
                                }
                            }
                        }
                        &:last-child {
                            tr {
                                &:last-child {
                                    // border-bottom: none;
                                }
                            }
                        }
                    }
                }
            }
        }
        .others {
            color: #050506;
            border-top: 1px solid #999;
            .univbb-widget__body {
                table {
                    tbody {
                        tr {
                            border-bottom: 1px solid #999;
                            td:before {
                                background-color: #999;
                            }
                            .game_wrap {
                                .inner_box:nth-of-type(2n):before {
                                    border-left: 1px solid #999;
                                }
                                .inner_box:nth-of-type(n+3) {
                                    // border-top: 1px solid #999;
                                }
                                .inner_box:nth-of-type(odd):before {
                                    border-right: 1px solid #999;
                                    @media only screen and (max-width : 768px){
                                                left: 0;
                                            }
                                }
                                .inner_box {
                                    border-top: none !important;
                                }
                                .link-others {
                                    a,span {
                                        display: block;
                                        width: 220px;
                                        padding: 12px 0;
                                        position: relative;
                                        font-size: 1.4em;
                                        margin: 8px auto 0;
                                        text-decoration: none;
                                        border-radius: 3px;
                                        font-weight: bold;
                                        padding-right: 10px;
                                        &:before {
                                            content:  " ";
                                            background-image: url(/stats/assets/univbb/img/right-arrow.png);
                                            display: inline-block;
                                            position: absolute;
                                            top: 14px;
                                            right: 70px;
                                            width: 7px;
