    @media only screen and (max-width : 768px){
        .univbb-widget {
            border-top: none;
            margin-top: 0;
            header {
                border-top: 1px solid $red;
                padding: 0;
                h2 {
                    font-size: 1em;
                }
            }
            .univbb-widget__body {
                table {
                    width: 100%;
                    thead {
                        th {
                            font-size: 1.2em;
                            font-weight: bold;
