                                    font-size: 1em;

                                }
                                .game_num {
                                    text-align: center;
                                    margin-bottom: 5px;
                                    font-size: 1em;
                                }
                                .card {
                                    font-size: 1em;
                                    font-weight: bold;
                                    text-align: center;
                                    display: flex;
                                    align-items: center;
                                    justify-content: center;
                                    .name {
                                        img {
                                            width: 8px;
                                            height: auto;
                                            vertical-align: middle;
                                        }
                                        &:nth-of-type(1) {
                                            img {
                                                margin-right: 2px;
                                            }
                                        }
                                        &:nth-last-of-type(1) {
                                            img {
                                                margin-left: 2px;
                                            }
                                        }
                                    }
                                    .score {
                                        margin: 0 2px;
                                    }
                                }
                                .link {
                                    display: flex;
                                    justify-content: space-between;
                                    margin-top: 5px;
                                    .btn {
