                                    .btn-active {
                                        background-color: #023483;
                                        color: #fff;   
                                    }
                                    .btn-non-active {
                                        background-color: #999;
                                        color: #fff;
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
